import React,{Component} from 'react';
import './style.css';
import Overall from './overall';
import SinglePortfolio from './single-portfolio';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';

class Portfolio extends Component {
  constructor(props) {
    super(props);
    this.state=({
    displayOveral:true,
    selectedProject:-1,
    selectedPage:0
    })
    this.overviewDisplay=this.overviewDisplay.bind(this);
    this.singleProjectDisplay=this.singleProjectDisplay.bind(this);
    this.selectProject=this.selectProject.bind(this);
    this.searchBarHandle=this.searchBarHandle.bind(this);
    this.selectPageHandle=this.selectPageHandle.bind(this);
  }
overviewDisplay(){
  return(
    <ReactCSSTransitionGroup
      transitionName="example"
      transitionEnterTimeout={300}
      transitionLeaveTimeout={300}>
    <Overall projects={this.props.projects} projectShow={6} projectHandler={this.selectProject}/>
    </ReactCSSTransitionGroup>
  )
}
singleProjectDisplay(idx){
  return(
    <ReactCSSTransitionGroup
      transitionName="example"
      transitionEnterTimeout={300}
      transitionLeaveTimeout={300}>
    <SinglePortfolio project={this.props.projects[idx]} key={idx} selectPageHandle={this.selectPageHandle} selectedPage={this.state.selectedPage}/>
    </ReactCSSTransitionGroup>
  )
}
selectProject(idx){
  this.setState({
    selectedProject:idx,
    displayOveral:false
  });
}
componentDidMount(){
  let scrollAmount = this.refs.portfolioSection.offsetTop;
  this.props.position(scrollAmount-5);
}
selectPageHandle(idx){
  this.setState({
    selectedPage:idx
  })
}
searchBarHandle(){
  let title = this.props.projects[this.state.selectedProject].title;
  let pageName;
  switch(this.state.selectedPage){
    case 1:
      pageName="details"
      break;
    case 2:
      pageName="gallery"
      break;
    case 3:
      pageName="testimonial"
      break;
    case 0:
    default:
      pageName="summery"
      break;
  }
  return title+"/"+pageName
}
backButtonHandle(){
  if(this.state.selectedPage===0){
    this.setState({displayOveral:true,selectedProject:-1})
  }else{
    this.setState({selectedPage:0})
  }
}
render(){
  return(
    <div>
      <div className="jumbotron portfolio-head" id="portfolio-section" ref="portfolioSection">
        <div className="container">
          <div className="row">
            <div className="col-xs-3 col-sm-2 col-md-1 txt-right">
              <span className="icon-return icn-margin-r" onClick={()=>this.backButtonHandle()}></span>
              <span className="icon-home icn-margin-r" onClick={()=>this.setState({displayOveral:true,selectedProject:-1,selectedPage:0})}></span>
            </div>
            <div className="col-xs-9 col-sm-10 col-md-11">
              <span className="search-box">root/portfolio/
                {this.state.selectedProject===-1?"":this.searchBarHandle()}
              </span>
            </div>
          </div>
        </div>
      </div>
      {this.state.displayOveral? this.overviewDisplay() : this.singleProjectDisplay(this.state.selectedProject)  }
    </div>
  )
}
}

export default Portfolio;



