import React,{Component} from 'react';
import './style.css';
import demo1 from './destineydentalDemo.jpg'
import demo2 from './Destiny.png'

const Gallery = ({images})=>{
  if(typeof images==="undefined" || images.length===0){
    return(<h2 className="not-avaliable">Not Avaliable</h2>)
  }else{
    let pos1=[];
    let pos2=[];
    let pos3=[];
    let pos4=[];
    images.map((image,idx)=>{
      switch(image.position){
        case "1":
          pos1.push(<img key={idx} src={image.url}/>)
          break;
        case "2":
          pos2.push(<img key={idx} src={image.url}/>)
          break;
        case "3":
          pos3.push(<img key={idx} src={image.url}/>)
          break;
        case "4":
          pos4.push(<img key={idx} src={image.url}/>)
          break;
        default:
          pos1.push(<img key={idx} src={image.url}/>)
          break;
      }
    })

    return(
      <div className="row gallery">
        <div className="col-sm-7">
          {pos1}
          <div className="row">
            <div className="col-xs-6">{pos3}</div>
            <div className="col-xs-6">{pos4}</div>
          </div>
        </div>
        <div className="col-sm-5">{pos2}</div>
      </div>
    )
  }
}

export default Gallery;



