import React,{Component} from 'react';
import './style.css';
import Summary from './summary';
import Details from './details';
import Gallery from './gallery';
import FeedBack from './feedback';

class SinglePortfolio extends Component {
  constructor(props) {
    super(props);
    this.state=({
      display:this.props.selectedPage
    })
    this.displayRender=this.displayRender.bind(this);
  }
  componentWillReceiveProps(nextProps) {
    console.log(nextProps.selectedPage,this.state.display)
    if (nextProps.selectedPage !== this.state.display) {
      this.setState({ display: nextProps.selectedPage });
    }
  }
  displayRender(state){
    const project = this.props.project;
    switch(state){
      case 1:
        return(<Details details={project.details} skills={project.filters}/>);
        break;
      case 3:
        return(<FeedBack testimonial={project.testimonial}/>);
        break;
      case 2:
        return(<Gallery images={project.gallery}/>);
        break;
      case 0:
      default:
        return(<Summary title={project.title} date={project.creationDate} description={project.description} imgUrl={project.featureImg}/>);
        break;
    }
  }
  render(){
    return(
      <div>
        <div className="jumbotron single-phead">
          <div className="container">
            <div className="row">
              <ul className="portfolio-features">
                <li className="col-xs-3">
                  <span
                    className={this.state.display===0? "active-feature" : ""}
                    onClick={()=>{this.props.selectPageHandle(0)}}> <i className="icon-monitor"></i>
                  </span>
                </li>
                <li className="col-xs-3">
                  <span
                    className={this.state.display===1? "active-feature" : ""}
                    onClick={()=>{this.props.selectPageHandle(1)}}><i className="icon-loupe"></i>
                  </span>
                </li>
                <li className="col-xs-3">
                  <span
                    className={this.state.display===2? "active-feature" : ""}
                    onClick={()=>{this.props.selectPageHandle(2)}}><i className="icon-image"></i>
                  </span>
                </li>
                <li className="col-xs-3">
                  <span
                    className={this.state.display===3? "active-feature" : ""}
                    onClick={()=>{this.props.selectPageHandle(3)}}><i className="icon-chat"></i>
                  </span>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div className="container single-pcontainer">
          {this.displayRender(this.state.display)}
        </div>
      </div>
    )
  }
}

export default SinglePortfolio;



