import React,{Component} from 'react';
import './style.css';
import Imac from './imac.png';


const Summary = ({title,date,description,imgUrl})=>{
  let showcaseStyle= {
    backgroundImage: 'url(' + imgUrl + ')',
    backgroundSize:"100% 60%",
    backgroundRepeat:"no-repeat",
    backgroundPosition: "0% 7%",
  }
  return(
    <div className="row portfolio-overview">
      <div className="col-sm-8">
        <h2 className="project-tittle"> {title} <time>{date}</time></h2>
        <div dangerouslySetInnerHTML={ {__html: description} } />
      </div>
      <div className="col-sm-4 project-showcase">
        <div className="showcase-preview-img">
          <img src={Imac} style={showcaseStyle}/>
          <div className="zoom-project-showcase">
            <img src={imgUrl}/>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Summary;



