import React,{Component} from 'react';
import './style.css';
import profile1 from './img/logo1.gif';
import profile1Hover from './img/logo1hover.gif';
import profile2 from './img/logo2.gif';
import profile2Hover from './img/logo2hover.gif';
import logoLine from './img/logo-line.svg';

class Main extends Component {
  constructor(props) {
    super(props);
    this.state={hover:false}
  }
  render(){
    return(
      <div className="jumbotron full-height sec-logo">
        <div className="container">
          <div className="row">
            <div className="main-logo"
              onMouseOver={()=>{this.setState({hover:true})}}
              onMouseOut={()=>{this.setState({hover:false})}}>
                <h1>
                 <span className="letter">S</span>
                 <img src={this.state.hover?profile1Hover:profile1}/>
                 <img src={this.state.hover?profile2Hover:profile2}/>
                 <span className="letter">R</span>
                 <span className="letter">I<sup>8</sup></span>
                </h1>
            </div>
          </div>
          <div className="row">
            <span onClick={()=>this.props.scrollHandler()} className='next-page'></span>
          </div>
        </div>
      </div>
    )
  }
}

export default Main;



