import React,{Component} from 'react';
import './style.css';
import PersonPush from './person-push.svg';
import SkillsGear from './gear-skill.svg';
import FreeGear from './free-gear.svg';

const Overview = ({fromMe})=>{
  return(
    <div className="about-me">
      <div className="row">
        <div className="col-sm-7 about-me-content">
          <h2>About Me <img className="person-push hidden-xs" src={PersonPush}/></h2>
            <div dangerouslySetInnerHTML={ {__html: fromMe} } />
        </div>
        <div className="col-sm-5">
          <img className="skill-gears" src={SkillsGear}/>
        </div>
      </div>
      <div className="row">
        <div className="col-xs-12 col-sm-9 ">
          <img src={FreeGear} className="free-gear-skill"/>
        </div>
      </div>
    </div>
  )
}


export default Overview;



