import React,{Component} from 'react';
import './style.css';
import DemoProfile from '../../portfolio/profile.jpg'

class Testimonial extends Component {
  constructor(props) {
    super(props);
    this.state=({
      currentslide:0
    })
    this.totalSlides = this.props.projects.length;
  }
  componentDidMount(){
    this.autoSlide=setInterval(
      ()=>{
        let currentslide=(this.state.currentslide+1)%this.totalSlides;
        this.setState({currentslide})
      },5000
    )
  }
  componentWillUnmount(){
    clearInterval(this.autoSlide);
  }
  testimonialsDisplay(idx){
    let project = this.props.projects[idx];
      return(
        <div className="testimonial-box" key={idx}>
          <div className="testimonial-txt-box">
            <p>{project.testimonial.text}</p>
          </div>
          <div className="testimonial-author">
            <img className="t-author-pic" src={typeof project.testimonial.profileImg==="undefined" || project.testimonial.profileImg.length===0? DemoProfile : project.testimonial.profileImg}/>
            <h3 className="t-author-name">{project.testimonial.author}</h3>
            <h4 className="t-author-tittle">{project.testimonial.title}</h4>
          </div>
        </div>
      )
  }
  slideChange(idx){
    this.setState({
      currentslide:idx
    })
  }
  pagnitionDisplay(){
    let pagnitions=[];
    for(let i=0;i<this.totalSlides;i++){
      pagnitions.push(<li key={i} className={this.state.currentslide===i?"t-pagnition-active":""} onClick={()=>this.slideChange(i)}></li>)
    }
    return pagnitions;
  }
  render(){
    return(
      <div className="others-about-me">
        <div className="row pagnition-row testimonial-pagnition">
          <ul>
            {this.pagnitionDisplay()}
          </ul>
        </div>
        {this.testimonialsDisplay(this.state.currentslide)}
      </div>
    )
  }
}

export default Testimonial;



