import React,{Component} from 'react';
import './style.css';
import DemoProfile from '../../profile.jpg';


const FeedBack = ({testimonial})=>{
  if(typeof testimonial==="undefined" || Object.keys(testimonial).length === 0){
    return (
      <h2 className="not-avaliable">Not Avaliable</h2>
    )
  }else{
    return (
      <div>
        <div className="row">
          <div className="feedback-box">
            <p>{testimonial.text}</p>
          </div>
        </div>
        <div className="row">
          <div className="feedback-author">
            <img className="f-author-pic" src={typeof testimonial.profileImg==="undefined" || testimonial.profileImg.length===0? DemoProfile : testimonial.profileImg}/>
            <h3 className="f-author-name">{testimonial.author}</h3>
            <h4 className="f-author-tittle">{testimonial.title}</h4>
          </div>
        </div>
      </div>
    )
  }
}

export default FeedBack;



