import React,{Component} from 'react';
import './style.css';

class ContactMe extends Component {
  constructor(props) {
    super(props);
    this.state=({
      difScroll:0,
      style:100
    })
    this.currentScroll = document.body.scrollTop;
  }
  componentDidMount(){
    let elementHeight=this.refs.contactMePos.scrollHeight;
    window.addEventListener('scroll',  ()=>this.handleScroll(elementHeight));
  }
  componentWillUnmount(){
    window.removeEventListener('scroll', this.handleScroll);
  }
  handleScroll(elementHeight){
    let elmentOffsetTop =this.refs.contactMePos.offsetTop;
    let startPos = elmentOffsetTop - window.innerHeight;
    let endPos = document.body.scrollHeight-window.innerHeight;
    let scrollTop = document.body.scrollTop;
    let step = Math.abs(scrollTop-this.currentScroll)*(100/elementHeight);
    if(scrollTop>=endPos-5){
      this.setState({
        style:0
      })
    }else if(scrollTop<startPos){
      this.setState({
        style:100
      })
    }else if(scrollTop>=startPos){
      if(this.currentScroll<scrollTop){
        this.setState({
          style:this.state.style-step
        })
      }else{
        this.setState({
          style:this.state.style+step
        })
      }
    }
    this.currentScroll=scrollTop;
  }
  render(){
    let contactInfo =this.props.contactInfo;
    let styleRTL = {
      transform: "translateX("+this.state.style+"%)",
      WebkitTransform: "translateX("+this.state.style+"%)",
      MozTransform: "translateX("+this.state.style+"%)",
    }
    let styleLTR = {
      transform: `translateX(-${this.state.style}%)`,
      WebkitTransform: `translateX(-${this.state.style}%)`,
      MozTransform: `translateX(-${this.state.style}%)`,
    }
    return(
      <div className="jumbotron contact-me" ref="contactMePos">
        <div className="jumbotron contact-name" style={styleLTR}>
              <div className="container">{contactInfo.name}</div>
        </div>
        <div className="jumbotron contact-phone" style={styleRTL}>
              <div className="container">
              {contactInfo.phone} | {contactInfo.email}
              </div>
        </div>
        <div className="jumbotron contact-address" style={styleLTR}>
              <div className="container">
              {contactInfo.address}
              </div>
        </div>
        <div className="jumbotron contact-social" style={styleRTL}>
              <ul className="container">
                <li ><a className="icon-facebook" href={contactInfo.social.facebook} title="facebook account"></a></li>
                <li ><a className="icon-linkedin-draw-logo" href={contactInfo.social.linkedin} title="facebook account"></a></li>
                <li ><a className="icon-github-draw-logo" href={contactInfo.social.github} title="facebook account"></a></li>
              </ul>
        </div>
      </div>
    )
  }
}

export default ContactMe;



