import React,{Component} from 'react';
import './style.css';
import DemoImg from './demo1.jpg';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';


const SingleProject =({title,subtitle,featureImg,projectHandler,index})=>{
  return(
    <div className="col-md-4 col-sm-6 col-xs-12">
      <div className="portfolio-box" onClick={()=>projectHandler(index)}>
        <div className="profile-thumbnail">
          <img src={featureImg} />
        </div>
        <div className="profile-desc">
          <h3>{title}</h3>
          <p>{subtitle}</p>
        </div>
      </div>
    </div>
  )
}


class Overall extends Component {
  constructor(props) {
    super(props);
    this.state = ({
      filters:[],
      currentSlide:1,
      projectsToShowIdx:[...Array(this.props.projects.length).keys()]
    })
    this.filters=[];
    this.filterProject = this.filterProject.bind(this);
    this.pagnitionHandle = this.pagnitionHandle.bind(this);
    this.projectCount=0;
  }
  componentWillMount(){
    this.props.projects.map((eachProject=>{
      let skills =eachProject.filters.filter(skill=>{
        return this.filters.indexOf(skill)<0
      })
      this.filters=this.filters.concat(skills);
    }))
  }
  filterProject(filter){
    let newFilter;
    if(filter==="all"){
      newFilter=[];
    }else{
      newFilter = this.state.filters;
      if(this.state.filters.indexOf(filter)>=0){
        newFilter.splice(this.state.filters.indexOf(filter),1);
      }else{
        newFilter.push(filter);
      }
    }
    this.setState({
      projectsToShowIdx:this.filterProjectToShow(newFilter),
      filters:newFilter
    })
  }
  filterProjectToShow(filters){
    this.projectCount=0;
    let newIdx=[];
    this.setState({
      currentSlide:1
    })
    if(filters.length===0){
      return [...Array(this.props.projects.length).keys()]
    }else{
      this.props.projects.map((eachProject,idx)=>{
        let state = eachProject.filters.some(filter=>{return filters.indexOf(filter)>=0});
        if(state){
          newIdx.push(idx);
        }
      })
      return newIdx;
    }
  }
  pagnitionHandle(idx){
    this.setState({
      currentSlide:idx
    })
  }
  render(){
    const props = this.props;
    let slidesCount = Math.floor(this.state.projectsToShowIdx.length/this.props.projectShow)
    if(this.state.projectsToShowIdx.length%this.props.projectShow!=0){
      slidesCount+=1;
    }
    let pagnition=[];
    for(let idx=1;idx<=slidesCount;idx++){
      pagnition.push(<li key={idx}
                        onClick={()=>this.pagnitionHandle(idx)}
                        className={this.state.currentSlide===idx? "active-pagnition" : ""}></li>)
    }
    let period=this.state.currentSlide*this.props.projectShow;
    let projectSlideFilter = this.state.projectsToShowIdx.filter((item,idx)=>{
      return (idx<period && idx>=period-props.projectShow)
    })
    return(
      <div className="container">
        <div className="row filter-row">
          <div className="col-xs-3 col-sm-2 col-md-1 txt-right">
            <h3>Filter:</h3>
          </div>
          <div className="col-xs-9 col-sm-10 col-md-11 mg-top-20">
            <span className={"skill-filter " +(this.state.filters.length===0? "skill-filter-active" : "")}
              onClick={()=>this.filterProject("all")}
              >All</span>
            {this.filters.map((filter,idx)=>{
              return (
                <span className={"skill-filter " +(this.state.filters.indexOf(filter)>=0? "skill-filter-active" : "")}
                  key={idx}
                  onClick={()=>this.filterProject(filter)}>{filter}</span>
              )
            })}
          </div>
        </div>
        <div className="row pagnition-row">
          <ul>
            {pagnition}
          </ul>
        </div>
        <div className="row portfolios-row">
          <ReactCSSTransitionGroup
            transitionName="example"
            transitionEnterTimeout={500}
            transitionLeaveTimeout={300}>

              {
                projectSlideFilter.map((ProjectIdx)=>{
                    return(
                      <SingleProject
                        title={this.props.projects[ProjectIdx].title}
                        subtitle={this.props.projects[ProjectIdx].subtitle}
                        featureImg={this.props.projects[ProjectIdx].featureImg}
                        key={ProjectIdx}
                        projectHandler={this.props.projectHandler}
                        index={ProjectIdx}/>
                    )
                  })
                }
          </ReactCSSTransitionGroup>
        </div>
      </div>
    )
  }
}




export default Overall;



