import React,{Component} from 'react';
import './style.css';
import Overview from './overview';
import Testimonial from './testimonial'

class AboutMe extends Component {
  constructor(props) {
    super(props);
    this.state=({
      testimonialShow:false
    })
  }
  render(){
    let projects = this.props.projects;
    let fromMe = this.props.fromMe;
    let TestimonialProjects = projects.filter((item,idx)=>{
      return (typeof item.testimonial !== "undefined" && Object.keys(item.testimonial).length !== 0)
    })

    return(
      <div>
        <div className={"jumbotron testimonial-head "+(this.state.testimonialShow?"testimonial-tab":"about-me-tab")}>
          <div className="container">
              <h2 onClick={()=>this.setState({testimonialShow:!this.state.testimonialShow})}><span className="tChangeable-tab">Others</span> About Me</h2>
          </div>
        </div>
        <div className="jumbotron testimonial-container">
          <div className="container">
            {this.state.testimonialShow? <Testimonial projects={TestimonialProjects}/>:<Overview fromMe={fromMe}/>}
          </div>
        </div>
      </div>
    )
  }
}


export default AboutMe;



