import React,{Component} from 'react';
import './style.css';

const Details = ({details,skills})=>{
  let detailsTxt;
  if(typeof details==="undefined" || details.length===0){
    detailsTxt=<h2 className="not-avaliable">Not Avaliable</h2>
  }else{
    detailsTxt=<div dangerouslySetInnerHTML={ {__html: details} } />
  }
  return(
    <div className="row">
      <div className="col-sm-3">
        <h2>More Details:</h2>
          <ul className="details-skills">
            {skills.map((skill,idx)=>{
              return(<li key={idx}>{skill}</li>)
            })}
          </ul>
      </div>
      <div className="col-sm-9 more-details">
          {detailsTxt}
      </div>
    </div>
  )
}

export default Details;



